.recipe{
    border-radius: 10px;
    box-shadow: 0px 5px 20px rgb(77, 76, 76);
    margin: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: white;
    align-items: center;
    min-width: 40%;
}
.image{
    border-radius: 50%;
    height: 200px;
    width: 200px;
}
.image:hover{
    transform: scale(1.3);
}
.list{
    font-family: cursive;
}
